:root {
  --dark-bg: #403c3c;
  --darker-bg: #7f4c4c;
  --input-bg: #444;
  --border-color: #666;
  --button-primary: #959194;
  --button-hover: #72646f;
  --text-color: #fff;
  --text-muted: #ccc;
}

* {
  margin: 0;
  padding: 10px;
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: var(--dark-bg);
  font-family: Arial, sans-serif;
  color: var(--text-color);
}

h5 {
  font-size: 15px;
}

h3 {
  margin: 0 0 15px 0;
}

a {
  list-style: none;
  text-decoration: none;
}

.donation-container {
  width: 400px;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  background-color: var(--darker-bg);
  margin: auto;
}

.donation-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.input-group {
  margin-bottom: 10px;
  width: 100%;
}

.input-field {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid var(--border-color);
  border-radius: 3px;
  box-sizing: border-box;
  background-color: var(--input-bg);
  color: var(--text-color);
  font-size: 15px;
}

.donate-button {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: var(--button-primary);
  color: var(--text-color);
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.donate-button:hover {
  background-color: var(--button-hover);
}

.contri-container {
  margin-top: 25px;
  text-align: center;
  border: 2px solid var(--border-color);
  background-color: #272a31;
  padding: 20px;
  border-radius: 7px;
  width: 100%;
}

.contri {
  background-color: #3f4756;
  border: 2px solid var(--border-color);
  padding: 1px;
  border-radius: 5px;
  color: #e5e5e5;
}

.contri p {
  font-size: 17px;
  margin: 1.7px 0;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-header {
  text-align: center;
}

.App h1 {
  color: var(--text-color);
  font-size: 32px;
}

.App h1 .blue {
  color: var(--button-primary);
}

.App h4 {
  color: var(--text-muted);
  margin-bottom: 20px;
}

.App-connection {
  color: var(--text-color);
}

.centered-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; /* Set the width to fill the parent container */
  height: 100vh;
}

.click {
  display: inline-block;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: var(--button-primary);
  color: var(--text-color);
  font-size: 16px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.click:hover {
  background-color: var(--button-hover);
}

.loading-animation {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-top: 4px solid #007bff;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin: 20px auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
